.button {
    position: relative;
    overflow: hidden;
    height: 2.6rem;
    padding: 0 2rem;
    border-radius: 0.6rem;
    background: #006DC1;
    background-size: 400%;
    color: #fff;
    border: none;
    cursor: pointer;
    border: none;
}

.button:hover::before {
    transform: scaleX(1);
}

.button-content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
}

.button::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: 0 50%;
    width: 100%;
    height: inherit;
    border-radius: none;
    background-color: #3391E6;
    transition: all 0.5s;
}